export default {
	data() {
		return {
			showPassword1: true,
			showPassword2: true,
			password1: '',
			password2: '',
			code: "",
			mobile: "",
			invitationCode:""
		}
	},
	onLoad(options) {
		let that = this;
		that.code = options.code;
		that.mobile = options.mobile;
		that.invitationCode = options.invitationCode;
	},
	methods: {
		changePassword1() {
			let that = this;
			that.showPassword1 = !that.showPassword1;
		},
		changePassword2() {
			let that = this;
			that.showPassword2 = !that.showPassword2;
		},
		register() {
			let that = this;
			if (!that.$utils.common.passwordTest.test(that.password1)) {
				uni.showToast({
					title: '密码需是8-14位数字或字母',
					icon: 'none'
				})
			} else if (that.password1 != that.password2) {
				uni.showToast({
					title: '两次密码不一致',
					icon: 'none'
				})
			} else {
				uni.showLoading({
					title:"请稍等..."
				})
				var param = {
					mobile: that.mobile,
					password: that.password1,
					repassword: that.password2,
					vcode: that.code,
					invitationCode:that.invitationCode
				}
				that.$api.bymobile(param).then(res => {
					uni.hideLoading();
					if (res.success) {
						uni.showToast({
							title: '注册成功',
							icon: 'success'
						})
						setTimeout(()=>{
							that.$Router.back(2);
						},2000)
					} else {
						uni.showToast({
							title: res.message,
							icon: "none"
						})
					}	
				})

			}
		},
		
		gotoAgreement1(){
			let that = this;
			that.$Router.push({
				path:"/pages/my/agreement/agreement1"
			})
		},	
		gotoAgreement2(){
			let that = this;
			that.$Router.push({
				path:"/pages/my/agreement/agreement2"
			})
		},	
	}
}
